.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #ffffffcc;
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.logo-section {
  display: flex;
  align-items: center;
}

.header-logo {
  height: 32px;
  margin-right: 10px;
}

.site-name {
  font-weight: 600;
  font-size: 1.2rem;
}

.nav-menu a {
  margin-left: 1rem;
  color: #1d1d1f;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-menu a:hover,
.nav-menu .active {
  color: #007aff;
}
